export default {
	props: {
		// 是否开启下边线
		border:{
			type:Boolean,
			default:true
		},
		// 封面
		cover: {
			type: String,
			default: ""
		},
		// 封面大小
		coverSize:{
			type: [String,Number],
			default:75
		},
		// 标题
		title:{
			type:String,
			default:""
		},
		// 显示右边
		showRight:{
			type:Boolean,
			default:false
		},
		// 显示左边图标
		showLeftIcon:{
			type:Boolean,
			default:true
		},
		// 是否显示箭头
		showRightIcon:{
			type:Boolean,
			default:true
		}
	},
	computed: {
		coverStyle() {
			return `width: ${this.coverSize}rpx;height: ${this.coverSize}rpx;`
		}
	},
}
